

.box {
  
  padding: 16px 24px;
  position: relative;
  border-radius: 8px;
  box-shadow: 0 0 0 1px rgba(0,0,0,.01);
}

.box::after {
  position: absolute;
  content: "";
  top: 15px;
  left: 0;
  right: 0;
  z-index: -1;
  height: 100%;
  width: 100%;
  transform: scale(01) translateZ(0);
  filter: blur(15px);
  background: linear-gradient(
    to left,
   
    /* #e4428d, */
    #c42da8,
    #9e16c3,
    #6501de,
    #9e16c3,
    #c42da8,
    /* #e4428d,
    #ff5770, */
    white,
    black
  );
  background-size: 200% 200%;
  animation: animateGlow 12s linear infinite;
}

@keyframes animateGlow {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 200% 50%;
  }
}
